<template>
	<div class="pa-md-6 pa-sm-3">
		<v-card>
      <v-card-title>
        {{ dataNotif.length }} Pemberitahuan
      </v-card-title>
      <v-card-text>
        <v-list>
          <v-list-item 
            v-for="item in dataNotif" :key="item.id"
            class="list-pemberitahuan">
            <v-list-item-content>
              <v-list-item-title>{{ item.name }}</v-list-item-title>
            </v-list-item-content>
            <v-list-item-icon>
              <div class="d-flex">
                <v-btn
                  depressed
                  @click="terima(item.id)"
                  color="blue lighten-3"
                  :loading="isLoading"
                  dark class="mr-3">
                  terima
                </v-btn>
                <v-btn
                  depressed
                  :loading="isLoading"
                  color="red lighten-3"
                  dark>
                  tolak
                </v-btn>
              </div>
            </v-list-item-icon>
          </v-list-item>
        </v-list>
      </v-card-text>
    </v-card>

    <Informasi :dialog="dialog" @hiddenDialog="hiddenDialog" />
	</div>
</template>

<script>
import Informasi from '../../components/Incubator/Pemberitahuan/Informasi'
import { mapState } from 'vuex'
export default {
  components: {
    Informasi
  },
  data: () => ({
    dialog: false
  }),
  methods: {
    hiddenDialog(val) {
      this.dialog = val
    },
    terima(val) {
      this.$store.dispatch('dataIncubatorNotif/postDataAccept', {
        id_startup: val
      })
      this.$store.dispatch('dataIncubatorNotif/updateIsLoading', true)
    }
  },
  created() {
    this.$store.dispatch('dataIncubatorNotif/getDataNotif')
    this.$store.dispatch('dataIncubatorNotif/updateIsLoading', true)
  },
  computed: {
    ...mapState('dataIncubatorNotif', ['isLoading', 'dataNotif', 'status'])
  },
  watch: {
    status(val) {
      if (val === 'warning') {
        this.dialog = true
      }
    }
  }
}
</script>

<style scoped>
  .list-pemberitahuan:nth-child(even){
    background-color: #f4f4f4;
  }
</style>